/**
 * CONFIDENTIALITÉ.
 */
import React from "react"
import { Link as GatsbyLink } from 'gatsby'
const config = require('../../gatsby-config')

/**
 * Page Meta.
 */
const pageMeta = {
  title: 'Politique de confidentialité',
  description: 'En utilisant notre site, vous reconnaissez que vous avez lu et compris notre politique de confidentialité.',
}

/**
 * Sublayout.
 */
const subLayout = {
  container: 'col'
}

const { title, siteUrl } = config.siteMetadata

/**
 * React component (JSX).
 */
const ConfidentialitePage = () => (
  <>
  <div id="content" className="container col">
    <div className="content">
      <h1>{pageMeta.title}</h1>
      <p>La présente politique de confidentialité ("politique") vous aidera à comprendre comment <b>{title}</b> ("nous", "notre", "nos") utilise et protège les données que vous nous fournissez lorsque vous visitez et utilisez <b>{siteUrl}</b> ("site web", "service").</p>
      <p>Nous nous réservons le droit de modifier cette politique à tout moment, dont vous serez informé dans les plus brefs délais. Si vous souhaitez vous assurer que vous êtes à jour des dernières modifications, nous vous conseillons de visiter fréquemment cette page.</p>

      <h2>Quelles données recueillons-nous?</h2>
      <p>Lorsque vous visitez le site web, nous pouvons recueillir les données suivantes :</p>
      <ul>
      <li>Votre adresse IP.</li>
      <li>Vos coordonnées et votre adresse électronique.</li>
      <li>D'autres informations telles que vos intérêts et vos préférences.</li>
      <li>Profil de données concernant votre comportement en ligne sur notre site web.</li>
      </ul>
      
      <h2>Pourquoi recueillons-nous vos données?</h2>
      <p>Nous recueillons vos données pour plusieurs raisons :</p>
      <ul>
      <li>Pour mieux comprendre vos besoins.</li>
      <li>Pour améliorer nos services et nos produits.</li>
      <li>Pour vous envoyer des courriels promotionnels contenant les informations que nous pensons que vous trouverez intéressantes.</li>
      <li>Pour vous contacter afin de remplir des enquêtes et de participer à d'autres types d'études de marché.</li>
      <li>Pour personnaliser notre site web en fonction de votre comportement en ligne et de vos préférences personnelles.</li>
      </ul>
      
      <h2>Sauvegarde et sécurisation des données</h2>
      <p><b>{title}</b> s'engage à sécuriser vos données et à les garder confidentielles. <b>{title}</b> a fait tout ce qui est en son pouvoir pour prévenir le vol de données, l'accès non autorisé et la divulgation en mettant en œuvre les technologies et les logiciels les plus récents, qui nous aident à protéger toutes les informations que nous recueillons en ligne.</p>

      <h2>Notre politique en matière de cookies</h2>
      <p>Lorsque vous acceptez que notre site web utilise des cookies, vous acceptez également d'utiliser les données qu'il collecte concernant votre comportement en ligne (analyse du trafic web, des pages web sur lesquelles vous passez le plus de temps et des sites web que vous visitez).</p>
      <p>Les données que nous recueillons en utilisant des cookies sont utilisées pour adapter notre site web à vos besoins. Une fois les données utilisées à des fins d'analyse statistique, elles sont complètement supprimées de nos systèmes.</p>
      <p>Veuillez noter que les cookies ne nous permettent pas de contrôler votre ordinateur de quelque manière que ce soit. Ils sont strictement utilisés pour contrôler les pages que vous trouvez utiles et celles que vous ne trouvez pas, afin que nous puissions vous offrir une meilleure expérience.</p>
      <p>Si vous souhaitez désactiver les cookies, vous pouvez le faire en accédant aux paramètres de votre navigateur internet. (Fournir les liens pour les paramètres des cookies pour les principaux navigateurs Internet).</p>

      <h2>Liens vers d'autres sites web</h2>
      <p>Notre site web contient des liens qui mènent à d'autres sites web. Si vous cliquez sur ces liens, <b>{title}</b> n'est pas responsable de la protection de vos données et de votre vie privée. La visite de ces sites n'est pas régie par la présente convention de confidentialité. Assurez-vous de lire la documentation relative à la politique de protection de la vie privée du site web auquel vous accédez à partir de notre site web.</p>

      <h2>Limitation de la collecte de vos données personnelles</h2>
      <p>À un moment donné, vous souhaiterez peut-être restreindre l'utilisation et la collecte de vos données personnelles. Vous pouvez y parvenir en procédant comme suit :</p>
      <p>Lorsque vous remplissez les formulaires sur le site web, assurez-vous de cocher une case que vous pouvez ne pas cocher, si vous ne souhaitez pas divulguer vos informations personnelles.</p>
      <p>Si vous avez déjà accepté de partager vos informations avec nous, n'hésitez pas à <GatsbyLink to="/fr/nous-joindre/">nous contacter</GatsbyLink> par courrier électronique et nous serons heureux de changer cela pour vous.</p>
      <p><b>{title}</b> ne louera, ne vendra ni ne distribuera vos informations personnelles à des tiers, sauf si nous avons votre autorisation. Nous pourrions le faire si la loi nous y oblige. Vos informations personnelles seront utilisées lorsque nous aurons besoin de vous envoyer du matériel promotionnel si vous acceptez cette politique de confidentialité.</p>
      <p>Ce document a été mis à jour pour la dernière fois le 30 juillet 2020</p>
    </div>
    <aside>

    </aside>
  </div>
  </>
)

ConfidentialitePage.pageMeta = pageMeta
ConfidentialitePage.subLayout = subLayout

export default ConfidentialitePage